@import url('https://fonts.googleapis.com/css?family=Roboto');

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 13px;
  color: #686867;

}
h1{
  text-align: center;
}
.agents-list{
  width: 80%;
  margin: auto;
  border: 1px #cccccc solid;
  border-radius: 5px;
}

.agents-container{
  border-top: 2px solid #dfdddd;
  border-bottom: 2px solid #dfdddd;

}

.agent.most-close{
  color: green;
}

.agent.most-far{
  color: red;
}

.table-header{
  background-color: #fbfafb;
  height: 50px;
  line-height: 50px;
  border-radius: 5px 5px 0 0 ;
}
.table-header .cell{
  font-weight: bold;
  border-right: 1px solid #dcddde;
  border-bottom: none;
}
.table-header .cell:last-child{
  border-right: none;

}
.footer{

  background-color: #fbfafb;
  height: 50px;
  line-height: 50px;


  text-align: right;
  padding-right: 20px;
  border-radius:  0 0 5px 5px;

}
.cell{
  display: inline-block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 40px;
  border-bottom:1px solid #dfdddd;
  line-height: 40px;
  padding-left: 10px;
}

.agentId{
  width: 10%;
}
.country{
  width: 10%;
}
.address{

  width: 50%;
}

.date{
  width: 30%;
}


.most-isolation{
  font-size: 18px;
  text-align: center;
  margin: auto;
  margin-top: 30px;
}

.most-isolation .country-name{
  font-weight: bold;
  color: green;
}


